* {
  margin: 0;
  padding: 0;
}

.Admin {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.admin__textarea,
.admin__input {
  width: 400px;
  font-family: 'Courier New', Courier, monospace;
  font-size: 1em;
}

.admin__btn {
  margin: 10px auto;
}

.admin__mt {
  margin-top: 30px;
}