* {
  margin: 0;
  padding: 0;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

button {
  display: block;
  padding: 0.75rem 3rem;
  background: #000;
  color: #fff;
  border-radius: 5px;
  border: 0;
  appearance: none;
  text-transform: uppercase;
  font-size: calc(1.29rem + .48vw);
  font-weight: 600;
  user-select: none;
  transition: all .3s cubic-bezier(.165,.84,.44,1);
  cursor: pointer;

  width: 400px;
}

button:hover {
  transform: scale(.95);
}

.button-container {
  display: flex;
  gap: 20px;
}

.button-container input {
  font-size: 1.5em;
  font-weight: bold;
  text-align: center;
}

.center {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
}

.countdown__inner {
  display: flex;
  font-size: 4.5em;
  font-weight: bold;
}

.countdown {
  margin-top: calc(1.525rem + 3.3vw);
  margin-bottom: calc(1.525rem + 3.3vw);
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  line-height: 1.2;
  margin: 0 0 16px;
}